/* index */
[data-tooltip] {
  position: relative; }
  [data-tooltip]:before, [data-tooltip]:after {
    z-index: 98;
    opacity: 0;
    visibility: hidden; }
  [data-tooltip]:after {
    content: attr(data-tooltip);
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    display: block;
    box-sizing: content-box;
    height: 12px;
    padding: 6px;
    font-size: 12px;
    white-space: nowrap;
    line-height: 1;
    color: #fff;
    text-shadow: 1px 1px 0 #000;
    background-color: grey; }
  [data-tooltip]:before {
    content: '';
    display: block;
    position: absolute;
    height: 0;
    width: 0;
    border-top: 6px solid grey;
    border-left: 3px solid transparent;
    border-right: 3px solid transparent;
    bottom: 100%;
    left: 50%;
    transform: translateX(-50%); }
  [data-tooltip]:after {
    bottom: 100%;
    margin-bottom: 6px; }
  [data-tooltip]:hover:before, [data-tooltip]:hover:after {
    opacity: 1;
    visibility: visible;
    transition: all 0.15s 0.2s; }

/* .index */
* {
