/* index */
[data-tooltip] {
	@include hui_tooltip(attr(data-tooltip), 'top', grey);
}
/* .index */

// font-family: 'Roboto', sans-serif;

* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
}

body, p, span, a, input, div, button {
	font-family: $inter;
}

p, span, a, div, button, li, svg {
	transition: all .3s linear;
}

div, section {
	background-size: cover;
	background-position: center;
	background-repeat: no-repeat;
}

h1:focus, h2:focus, h3:focus, h4:focus, h5:focus, h6:focus, p:focus, 
span:focus, a:focus, input:focus, button:focus, div:focus {
	outline: none;
}

div, button, input, a, p, span, h1, h2,
h3, h4, h5, h6 {
	margin: 0;
	-webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

a {
	text-decoration: none;
}

li {
	list-style-type: none;
}

.wrap-site {
	margin: 0 auto;
	max-width: 1440px;
	overflow: hidden;
}

.menu-fixed {
	position: fixed !important;
}

.visibility-hidden {
	margin: 0;
	height: 0;
	visibility: hidden;
}

