}*/

.margin-top-none {
	margin-top: 0 !important;
}

.main {
	width: 100%;
	border-radius: 0 0 100px 100px;
	background-color: #0D4290;
	position: relative;
	&__background {
		width: 504px;
		height: 504px;
		border-radius: 50%;
		background: rgba(255, 255, 255, 0.25);
		filter: blur(200px);
		left: -190px;
		top: -156px;
		position: absolute;
		pointer-events: none;
	}
	&__content {
		margin: 0 auto;
		padding: 80px 0 0;
		max-width: 1160px;
		width: 100%;
	}
	&__blockLogotypes,
	&__head {
		padding: 0 30px;
	}
	&__blockLogotypes {
		display: flex;
		justify-content: space-between;
	}
	&__logotype-url {
		width: 19.54545454545455%;
		max-height: 50px;
	}
	&__logotype {
		width: 100%;
		max-height: 50px;
		&.language {
			width: 19.36363636363636%;
		}
	}
	&__head {
		margin-top: 95px;
		width: 84%;
		font-size: $fs_48px;
		font-weight: $fw_vbig;
		line-height: $lh_58;
		color: white;
	}
	&__program {
		margin-top: 20px;
		padding: 0 20px 40px;
		display: flex;
		flex-wrap: wrap;
	}
	&__iconsblock,
	&__videoblock {
		padding: 0 10px;
		width: 50%;
	}
	&__item {
		margin-top: 20px;
		display: flex;
		align-items: center;
	}
	&__itemicon {
		margin-right: 10px;
		width: 48px;
		height: 48px;
	}
	&__itemtext {
		font-size: $fs_18px;
		line-height: $lh_22;
		color: white;
	}
	&__button {
		margin-top: 40px;
		padding: 20px;
		background-color: #FFE352;
		cursor: pointer;
		font-weight: $fw_big;
		line-height: $lh_19;
		border-radius: 10px;
		border: none;
	}
	&__videoblock {
		display: flex;
		justify-content: flex-end;
	}
	&__image {
		width: 96.36363636363636%;
	}
	&__video {
		margin-top: 55px;
		width: 100%;
	}
}

#program {
	position: relative;
}

.programtop {
	margin: 125px auto 0;
	max-width: 1100px;
	min-height: 480px;
	padding: 60px 90px 55px;
	border-radius: 20px 20px 0 0;
	background-color: #112133;
	position: relative;
	&__background {
		width: 385px;
		height: 385px;
		top: -50px;
		right: -52px;
		position: absolute;
		pointer-events: none;
	}
	&__head,
	&__text {
		width: 65.21739130434783%;
		color: white;
	}
	&__head {
		font-size: $fs_24px;
		font-weight: $fw_med;
		line-height: $lh_34;
	}
	&__text {
		margin-top: 30px;
		font-size: $fs_18px;
		line-height: $lh_26;
		&:nth-child(2) {
			margin-top: 40px;
		}
	}
}

.programbottom {
	margin: 0 auto;
	padding: 0 90px 70px;
	max-width: 1100px;
	display: flex;
	border-radius: 0px 0px 20px 20px;
	background-color: #F3F6F9;
	&__head {
		margin-top: 70px;
		font-size: $fs_24px;
		line-height: $lh_34;
		color: $accordoin_color;
	}
	&__accordionblock {
		width: 65.21739130434783%;
	}
	&__accordion {
		margin-top: 20px;
	}
	&__paragraph {
		margin-top: 10px;
		padding: 0 10px;
		height: 50px;
		display: flex;
		align-items: center;
		border: 1px solid rgba(118, 148, 174, 0.3);
		border-radius: 4px;
		color: $accordoin_color;
		cursor: pointer;
		&:hover {
			background-color: white;
		}
		&.active {
			background-color: white;
			& .programbottom__arrow {
				transform: scale(1);
			}
		}
	}
	&__arrow {
		margin-right: 10px;
		width: 13px;
		height: 8px;
		transform: scale(-1);
	}
	&__accordionhead {
		font-size: $fs_16px;
		font-weight: $fw_big;
		line-height: $lh_24;
	}
	&__accordoinlist {
		height: 0;
		overflow: hidden;
	}
	&__list {
		padding: 10px 0 20px 25px;
	}
	&__item {
		font-size: $fs_16px;
		line-height: $lh_24;
		list-style-type: disc;
		color: $accordoin_color;
	}
	&__tablet {
		padding-left: 60px;
		width: 34.78260869565217%;
		display: flex;
		justify-content: flex-end;
	}
	&__image {
		margin-top: 50px;
		width: 80.91603053435115%;
		max-height: 300px;
	}
}

.background-block {
	width: 100%;
	height: 46.6887417218543%;
	bottom: 0;
	position: absolute;
	background-color: #E3EAF0;
	z-index: -1;
}

.competition {
	margin-top: 95px;
	min-height: 120px;
	padding: 30px 0;
	display: flex;
	position: relative;
	background: linear-gradient(0deg, #FFE352, #FFE352);
	&__blockimage {
		width: 25.34722222222222%;
		min-height: 100%;
	}
	&__backgroundimage {
		width: 22.5%;
		height: 100%;
		top: 0;
		left: 0;
		bottom: 0;
		position: absolute;
	}
	&__block {
		margin: 0 85px 0 70px;
		width: 74.65277777777778%;
		display: flex;
		justify-content: space-between;
		align-items: center;
		z-index: 2;
	}
	/*&__head {
		padding: 0 30px;
		width: 18.18181818181818%;
		font-weight: $fw_med;
		font-size: $fs_30px;
		line-height: $lh_34;
		text-transform: uppercase;
		color: #FE5955;
	}*/
	&__text {
		padding-right: 15px;
		//width: 70.19730010384216%;
		font-size: $fs_22px;
		font-weight: $fw_min;
		line-height: $lh_28;
		text-align: center;
		color: rgba(0, 0, 0, 0.72);
	}
	&__bold {
		font-weight: $fw_med;
	}
	&__blockbutton {
		padding-left: 15px;
		display: flex;
		justify-content: flex-end;
	}
	&__button {
		padding: 10px 30px;
		min-width: 241px;
		background-color: #FE5955;
		box-shadow: 0px 4px 24px rgba(162, 54, 51, 0.25);
		border-radius: 15px;
		border: none;
		font-weight: $fw_big;
		font-size: $fs_20px;
		line-height: $lh_28;
		color: white;
		cursor: pointer;
	}
}

@media all and (max-width: 1200px) {
	.main {
		border-radius: 0 0 50px 50px;
	}
}