.header {
	width: 100%;
	height: 60px;
	display: flex;
	align-items: center;
	background-color: rgba(11, 55, 119, 0.8);
	position: absolute;
	top: 0;
	left: 0;
	right: 0;
	z-index: 1;
	&__menu {
		margin: 0 auto;
		max-width: 1325px;
		padding: 0 30px 0 15px;
		width: 100%;
		display: flex;
		justify-content: space-between;
	}
	&__wrapper {
		//max-height: 34px;
		display: flex;
		&.menu {
			min-width: 60.46153846153846%;
		}
	}
	&__list {
		width: 100%;
		display: flex;
		justify-content: space-between;
	}
	&__item {
		margin-right: 10px;
		padding: 8px 15px;
		display: flex;
		&:hover,
		&.active {
			background-color: white;
			& .header__link {
				color: black;
				&.language {
					color: rgba(255, 255, 255, 0.6);
				}
			}
		}
		&.button {
			padding: 0;
			background-color: transparent;
			& .header__link {
				color: white;
			}
		}
		&.language {
			margin-right: 25px;
			background-color: transparent;
		}
		&:last-child {
			margin-right: 0px;
		}
	}
	&__link {
		font-size: $fs_16px;
		line-height: $lh_18;
		font-family: $ptMono;
		color: white;
		display: flex;
		align-items: center;
		&.language {
			color: rgba(255, 255, 255, 0.6);
		}
	}
	&__button {
		padding: 6px 10px 6px 15px;
		min-width: 182px;
		display: flex;
		font-size: $fs_16px;
		font-family: $ptMono;
		font-weight: $fw_vbig;
		line-height: $lh_18;
		align-items: center;
		border: 1px solid white;
		background-color: transparent;
		color: white;
		cursor: pointer;
		&:hover {
			color: black;
			background-color: white;
		}
	}
	&__arrow {
		margin-right: 5px;
		width: 5px;
		height: 10px;
		&.button {
			margin-left: 10px;
		}
	}
}